@import url(https://fonts.googleapis.com/css2?family=Tinos&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Tinos', serif;
}


.login-container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    
}

.login-form{
    display: flex;
    flex-direction: column;
    border: 2px solid rgb(211, 211, 211);
    border-radius: 7px;
    padding: 10px;
}

.login-form h2{
    color: #272727;
}


.login-form input[type="submit"]{
    margin: 0 auto;
    margin-top: 10px;
    width: 50%;
    color: rgba(223, 223, 223, 0.87);
    background-color: #545454;
    border: none;
    padding: 5px;border-radius: 7px;
    font-weight: bold;
}

.login-container span{
    margin-top: 5px;
    font-size: .9rem;
    color: #252525;
}

.login-container span:hover{
    cursor: pointer;
}
.login-container{
    height: calc(100vh - 50px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width:100%;
    
}

.login-form{
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    
    border-radius: 7px;
    padding: 10px;
}

.login-form h2{
    color: #323232;
}


.login-form input[type="submit"]{
    margin: 0 auto;
    margin-top: 10px;
    width: 50%;
    color: rgb(255, 255, 255);
    background-color: #323232;
    border: none;
    padding: 5px;border-radius: 7px;
    font-weight: bold;
}

.login-container span{
    margin-top: 5px;
    font-size: .9rem;
    color: #676767;
}

.login-container span:hover{
    cursor: pointer;
}

.error{
    color: red;
    margin-bottom: .5rem;
}
.loading{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    min-width: 100vw;
    position: absolute;
    top:0;
    left: 0;
    z-index: 99;
    background-color: rgba(255, 255, 255, 0.501);
}
nav{
    min-height: 50px;
    display: flex;
    align-items: center;
    border-bottom: 2px solid #7e7e7e;
    justify-content: space-between;
    box-sizing: border-box;
}
.logout{
    border: 2px solid rgb(255, 68, 68);
    padding: .2rem .4rem;
    margin-right: .5rem;
    border-radius: 7px;
    color: rgb(255, 18, 18) !important;
    transition: .3s;
    display: flex;
    align-items: center;
}
.logout:hover{
    background-color:rgb(255, 68, 68) ;
    color: #fff !important;
    cursor: pointer;
    transition: .3s;
}

nav h1{
    padding-left: 15px;
    margin: 0;
    color:#1794fa;
}
.logo-container{
    display: flex;
    align-items: center;
}
.logo{
    width: 10rem;
    margin-left: 1rem;
}
.profile{
    display: flex;
    align-items: center;
    padding: 0 .5rem;
}
.profile .icon{
    font-size: 2rem;
    margin-right: .3rem;
}
.home-container{
    display: flex;
    max-height: calc(100vh - 50px);
    flex-direction: row;
    box-sizing: border-box;
    overflow-y: auto;
    box-sizing: border-box;
    position: relative;
}

.sidebar{
    display: none;
    padding: relative;
}
.open{
    display: block !important;
    background-color: rgba(255, 255, 255, 0.496);
    z-index: 9999;
    position: absolute;
    top: 10px;
    left: 10px;
    width: 100%;
}

.filter{
    position: absolute;
    top: 5px;
    left: 5px;
    background-color: rgb(27, 27, 27);
    color: #fff;
}

.filter-close{
    position: absolute;
    top: 10px;
    left: 250px;
    z-index: 999999;
    background-color: rgb(255, 55, 55);
    color: #fff;
}

@media screen and (min-width:768px){
    .sidebar{
        display: block;
    }
    .filter{
        display: none;
    }

}
.section-listing-container{
    width: 250px;
    max-height: calc(100vh - 50px);
    list-style-type: none;
    overflow-y: auto;
    padding-bottom: 1rem;
    background-color: #fff;
    box-sizing: border-box;
}
.section-link{
    
    padding: .5rem;
    padding-left: 2rem;
    text-align: left;
    border-bottom: 1px solid rgb(199, 199, 199);
}
.active{
    background-color: rgb(209, 209, 209);
}
.section-link:hover{
    cursor: pointer;
}
.news-article-container{
    max-height: calc(100vh - 50px);
    overflow-y: auto;
    margin: 1rem 1rem;
    margin-top: 2rem;
    flex-grow: 1;
}
.article-wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.acticle{
    /* width: 100%; */
    padding: 1rem;
    padding-bottom: 0;
    display: flex;
    flex-direction: row;

}
.article-section{
    flex-grow: 1;

}
section h3{
    margin: 0 0 1rem .5rem;
}
section p{
    margin: 0 0 1rem .5rem;
}
a{
    text-decoration: none;
    color: #000;

}
.action{
    display: flex;
    justify-content: space-between;
    
}

button{
    padding: .3rem 1rem;
    border: 1px solid rgb(199, 199, 199);
    border-radius: .3rem;
    font-size: .9rem;
    align-self: flex-end;
    margin: 0 .5rem .5rem 0;
}
small{
    margin: 0 0 1rem .5rem;
    color: rgb(77, 77, 77);
}

@media screen and (min-width:768px){
    .news-article-container{
        margin-top: 1rem;
    }

}
.pagination{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: row;
    margin: 1rem .5rem 1rem 0;
    overflow-y: auto;
    background-color: #ffffff;
}

.pagination .page-item{
        height: 25px;
        width: 25px;
        border: 1px solid rgb(102, 102, 102);
        padding: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bolder;
        color: #6f7780;
    }
.pagination .page-item:hover{
        border: 1px solid rgb(70, 70, 70);
        cursor: pointer;
    }

.pagination .active{
        background-color: rgb(84, 84, 84);
        color: #ffffff;
    }
    

.profile-container{
    height: calc(100vh - 50px);
    display: flex;
    align-items: center;
    padding: 1rem;
    flex-direction: column;
    margin-top: 3rem;
    position: relative;
}
.profile-container .icon{
    font-size: 5rem;
}
.profile-container span{
    padding: .5rem;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
}
.profile-container span .icon{
    margin-right: 1rem;
    font-size: 1.1rem;

}

.modal-container{
    width:80%;
    margin: 0 .5rem;
    background-color: goldenrod;
}

.Modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    width: 80%;
    height: 70vh;
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
  }

  .Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.36);
    box-sizing: border-box;
  }

  .modal-content{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      box-sizing: border-box;
      /* height: 100%; */
  }

  form{
      display: flex;
      justify-content: center;
      padding: 1rem;
      flex-direction: column;
      align-items: center;
      border: 1px solid gray;
      border-radius: .5rem;
      margin-bottom: 1rem;
      min-width: calc(min(300px,80%));
      box-sizing: border-box;
  }
  .modal-content h3{
      font-size: 1.5rem;
  }
  .modal-content  input{
      font-size: 1rem;
      margin: .5rem 0;
      padding: 0 .5rem;
  }

.modal-action{
    display: flex;
    justify-content: flex-end;
    font-size: 2rem;
    margin: .5rem;
    color: gray;
}

@media screen and (min-width:768px){
    .modal-container{
        width:40%;
    }
}
.no-saved-news{
    height: calc(100vh - 50px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.saved-news-container{
    display: flex;
    margin: 1rem auto;
    flex-direction: column;
    max-width: calc(min(700px,80%));
    
}

.news{
    display: flex;
    border: 1px solid gray;
    align-items: center;
    padding: 1rem;
    margin-bottom: .5rem;
    border-radius: .5rem;
    
}

.news a{
    flex: 5 1;
    transition: .5s;
}
.news a:hover{
    transform: scale(1.01);
    transition: .5s;
}

.news button{
    flex: 1 1;
}

.delete{
    background-color: rgb(238, 41, 41);
    color: #fff;
}
